<template>
	<drawer-bottom
		ref='drawerRef'
		title="取货时间"
	>
		<view class="choose-pickup-time">
			<view class="date-time-tables">
				<view class="column left">
					<view v-for="(item, index) in dateRanges" :key="index" :class="['column-item', {active: columnIndex === index  }]" @click="toggleDate(index)">
						<text>{{ item.text }}</text>
					</view>
				</view>
				<view class="column right">
					<view v-for="(item, index) in timeRanges" :key="index" :class="['column-item', {active: columnIndex2 === index  }]" @click="selectDate(item)">
						<text>{{ item.text }}</text>
					</view>
				</view>
			</view>
		</view>
	</drawer-bottom>
</template>

<script>
	import DrawerBottom from '@/components/drawer-bottom/index.vue'
	import { dateFormat } from '@/frame/utils/date-format'
	
	const baseHourSteps = [
		{
			start: 9,
			end: 11,
			text: '09:00-11:00'
		},
		{
			start: 11,
			end: 13,
			text: '11:00-13:00'
		},
		{
			start: 13,
			end: 15,
			text: '13:00-15:00'
		},
		{
			start: 15,
			end: 17,
			text: '15:00-17:00'
		},
		{
			start: 17,
			end: 19,
			text: '17:00-19:00'
		},
		{
			start: 19,
			end: 21,
			text: '19:00-21:00'
		}
	]
	
	const dateTips = [
		'（今天）',
		'（明天）',
		'（后天）',
		'（大后天）'
	]
	
	const HOURS = 4
	const HOURS_STAMP = 1000 * 60 * 60 * HOURS
	
	export default {
		components: {
			DrawerBottom
		},
		props: {
			dateCount: {
				type: Number,
				default: 3
			}
		},
		data() {
			return {
				// 日期列的index
				columnIndex: 0,
				columnIndex2: -1,
				dateRanges: []
			}
		},
		computed: {
			timeRanges() {
				return this.dateRanges[this.columnIndex]?.children ?? []
			}
		},
		methods: {
			selectDate(item) {
				this.$emit('confirm', item.value)
				this.hide()
			},
			toggleDate(index) {
				this.columnIndex = index
				this.columnIndex2 = -1
			},
			genTimeData(selectDateString) {
				this.columnIndex = 0
				const dateRanges = []
				const dateIns = new Date()
				const dateInsStamp = dateIns.getTime()
				
				const validStamp = dateInsStamp + HOURS_STAMP
				
				const year = dateIns.getFullYear()
				const month = dateIns.getMonth()
				const date = dateIns.getDate()
				for (let i =0;i<this.dateCount;i++) {
					const dateString = dateFormat(new Date(year, month, date + i), 'yyyy-MM-dd')
					
					// 过滤出结束时间在这之前的
					const result = baseHourSteps
						.filter(item => {
							const endStamp = new Date(year, month, date + i, item.end).getTime()
							return endStamp > validStamp
						}).map((item, index) => {
							const value = dateString + ' ' + item.text
							
							if (value === selectDateString) {
								// 选中的数据
								this.columnIndex = i
								this.columnIndex2 = index
							}
							
							return {
								...item,
								value
							}
						})
					
					const tip = dateTips[i] || ''
					dateRanges.push({
						text: dateString + tip,
						children: result
					})
				}
				this.dateRanges = dateRanges
			},
			show(dateString) {
				this.$refs.drawerRef.show()
				this.genTimeData(dateString)
			},
			hide() {
				this.$refs.drawerRef.hide()
			}
		}
	}
</script>

<style lang="scss" scoped>
	.date-time-tables{
		display: flex;
		height: 600upx;
		font-size: 28upx;
	}
	
	.column {
		box-sizing: border-box;
		flex-shrink: 0;
		flex-grow: 1;
		flex-basis: 50%;
		
		&.left {
			background-color: rgb(245, 245, 245);
		}
		
	}
	
	.column-item {
		padding: 22upx 40upx;
		color: #999;
		&.active {
			color: $theme-color;
			font-weight: 600;
			background-color: #fff;
		}
	}
</style>
